<template>
	<div id="store">
		<public-header title="商户列表"></public-header>
		<public-footer :menufooter="menufooter" :showPreLoading="showPreLoading"></public-footer>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.is_rest == -1}" @click="onToggleStatus(-1)">全部</div>
					<div class="tab-item" :class="{active: filter.items.is_rest == 0}" @click="onToggleStatus(0)">营业中</div>
					<div class="tab-item" :class="{active: filter.items.is_rest == 1}" @click="onToggleStatus(1)">休息中</div>
				</div>
				<van-search placeholder="请输入店铺ID或店铺名称" v-model="filter.items.keywords" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="store-list"
				>
					<div class="store-item" v-for="(item, index) in records.data" :key="item.id">
						<router-link tag="div" :to="util.getUrl({path: '/pages/merchant/store/index', query: {id: item.id}})" class="data flex-lr padding-15-b border-1px-b">
							<div class="logo">
								<div class="label" v-if="item.label > 0">{{item.label_cn}}</div>
								<img class="img-100" :src="item.logo" alt="">
							</div>
							<div class="info">
								<div class="title-status">
									<div class="title font-15 ">{{item.title}}</div>
									<div class="itag" :class="{'itag-danger': item.is_in_business == 1, 'itag-disabled': item.is_in_business == 0}">{{item.is_in_business == 1 ? '营业中' : '休息中'}}</div>
								</div>
								<div class="telephone c-info font-13"@click.stop="util.jsUrl('tel:' + item.telephone)">电话: {{item.telephone}}</div>
								<div class="flex-lr c-gray font-13">
									<div class="account">账户余额: ¥{{item.amount}}</div>
									<div class="deposit">保证金: ¥ {{item.deposit}}</div>
								</div>
								<ul class="record c-gray font-12">
									<li class="flex">
										<i class="icon icon-activity c-primary"></i>
										<span>销量: {{item.sailed}}</span>
									</li>
									<li>
										<i class="icon icon-hotfill c-danger"></i>
										<span>热度: {{item.click}}</span>
									</li>
									<li>
										<i class="icon icon-order1 c-info"></i>
										<span>排序: {{item.displayorder}}</span>
									</li>
								</ul>
							</div>
						</router-link>
						<div class="operate padding-15-t flex-lr">
							<iswitch :value="item.is_in_business" text-open="营业" @change="jsToggleSwitch" :extra="{key: 'records.data.' + index + '.is_in_business', id: item.id, name: 'is_in_business'}"></iswitch>
							<iswitch :value="item.is_recommend" text-open="推荐" @change="jsToggleSwitch" :extra="{key: 'records.data.' + index + '.is_recommend', id: item.id, name: 'is_recommend'}"></iswitch>
							<iswitch :value="item.is_stick" text-open="置顶" @change="jsToggleSwitch" :extra="{key: 'records.data.' + index + '.is_stick', id: item.id, name: 'is_stick'}"></iswitch>
						</div>
					</div>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import iswitch from "@/components/switch"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				storeTitle: '',
				status: {
					is_show: true,
					is_recommend: true,
					is_stick: false,
				},

				records: {
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data: []
				},
				keywords: '',
				filter: {
					items: {
						is_rest: -1
					}
				},
				showPreLoading: true,
				isRefresh: false,
				menufooter: {}
			}
		},
		components: {
			publicHeader,
			publicFooter,
			iswitch
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/merchant/store/list',
					data: {
						menufooter: 1
					}
				});
			},
			onToggleStatus: function(value) {
				value = parseInt(value);
				if(this.filter.items.is_rest == value) {
					return;
				}
				this.filter.items.is_rest = value;
			},
			onPullDownRefresh() {
				this.onLoad(true);
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					url: 'plateform/merchant/store/change',
					key: res.key || res.keys,
					data: {
						id: res.id,
						name: res.name,
						value: res.value
					}
				});
			}
		},

		mounted() {
			this.onLoad();
			this.util.imap();
			this.util.icloudapi();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#store .van-pull-refresh{
		overflow: visible;
	}
	#store .van-search{
		background-color: #f5f5f5!important;
	}
	#store .van-search .van-icon-search{
		top: 25px;
	}
	#store .store-item{
		margin: 10px 10px;
		padding: 10px;
		background-color: #fff;
		border-radius: 5px;
		font-size: 14px;
	}
	#store .store-item .logo{
		width: 80px;
		height: 80px;
		border-radius: 5px;
		overflow: hidden;
		position: relative;
	}
	#store .store-item .logo .label{
		position: absolute;
		top: 10px;
		left: -25px;
		z-index: 1;
		padding: 2px 30px;
		background-color: #4FAE52;
		color: #fff;
		transform: rotate(-45deg);
		font-size: 12px;
	}
	#store .store-item .info{
		height: 80px;
		width: 73%;
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-between;
	}
	#store .store-item .info .title-status{
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: space-between;
	}
	#store .store-item .info .record{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		margin-top: 5px;
	}
	#store .store-item .info .record li{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		margin-right: 10px;
	}
</style>


